<template>
    <main class="hide-overflow">
        <div class="error-div">
            <h1>{{$route.params.code}}</h1>
            <h2>{{getTitle}}</h2>
            <p v-html=getBody></p>
            <p class="actions">
                <router-link to="/" class="btn" v-if="$route.params.code !='500'">Revenir à l'accueil</router-link>
                <router-link to="/" class="btn" v-else>Réessayer</router-link>
                <router-link to="/login" v-if="$route.params.code =='404'">Peut être que vous aimeriez la rajouter?</router-link>
                <router-link to="/login" v-if="$route.params.code =='401'">Se connecter</router-link>
            </p>
        </div>
    </main>
</template>
<script>
import { mapGetters } from 'vuex'

export default {
    data () {
        return{
            errors: {
                400: {
                    title: "Structure de la requête incorrecte",
                    body: "La requête que vous avez émise comporte des erreurs syntaxiques et n'as pas pu être traitée par le serveur."
                },
                401: {
                    title: "Authentification requise",
                    body: "Merci de vous connecter pour accéder à l'interface d'amdinistration."
                },
                403: {
                    title: "Opération non autorisée"
                },
                404: {
                    title: "Page introuvable",
                    body: "La fiche demandée n'existe pas."
                },
                429: {
                    title: "Trop de requêtes",
                    body: "Merci de temporiser vos actions/modifications sur le site."
                },
                498: {
                    title: "Session expirée",
                    body: "Merci de vous reconnecter."
                },
                500: {
                    title: "Erreur serveur",
                    body: "Cette erreur provient du serveur, elle sera bientôt corrigée"
                },
            },
            permsLevel: {
                0: "Vous devez attendre la validation de votre compte pour pouvoir ajouter des fiches.",
                1: "En tant d'utilisateur, vous pouvez uniquement créer un auteur ou rajouter une œuvre.",
                2: "En tant que rédacteur, vous pouver créer, modifier et supprimer une fiche (auteur ou œuvre)."
            }
        }
    },
    computed: {
        ...mapGetters('jwt',[
            'jwt',
            'level'
        ]),
        getTitle: function() {
            if (this.errors[this.$route.params.code]) {
                return this.errors[this.$route.params.code].title
            } else {
                return "Une erreur est survenue"
            }
        },
        getBody: function() {
            if (this.$route.params.code == 403) {
                return this.getPermissions
            } else if (this.errors[this.$route.params.code]) {
                return this.errors[this.$route.params.code].body
            } else {
                return "Merci de réessayer plus tard."
            }
        },
        getPermissions: function() {
            let body = "Vous n'avez pas les permissions suffisantes avec ce compte pour réaliser cette action."
            if (this.jwt && this.level && this.permsLevel[this.level]) {
                body+="<br>"+this.permsLevel[this.level]
            } 
            return body
        }
    }
}
</script>

